@use '../variables' as *;

.card {
    display: block;
    padding: $base-padding;
    border: $base-border-thickness solid #ddd;
    box-shadow: $base-box-shadow;

    &__title {
        font-size: $base-font-size;
        padding-bottom: $base-padding;
        font-weight: bold;
    }

    &__body {
        font-size: $base-font-size;

        a {
            text-decoration: underline;
        }
    }
}

